/* COLORS */
/* FONTS */
@import url("https://fonts.googleapis.com/css?family=Lato:400,700");
html, body {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 0;
  line-height: 24px; }

a {
  color: #232426;
  text-decoration: none; }
  a:hover, a:focus {
    color: #0085DA; }

h1, h2, h3, h4, h5, h6 {
  color: #16181E;
  font-weight: 700; }

h1 {
  font-size: 1.7rem; }

h2 {
  font-size: 1.5rem; }

h3 {
  font-size: 1.3rem; }

h4 {
  font-size: 1.2rem; }

h5 {
  font-size: 1.1rem; }

h6 {
  font-size: 1rem; }

p {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0;
  color: #16181E;
  line-height: 24px; }

p.small {
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0;
  color: #16181E;
  line-height: 20px; }

small {
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0;
  color: #B0BAC5;
  line-height: 20px; }

hr {
  margin-top: 10px;
  margin-bottom: 10px;
  border: 0;
  border-top: 1px solid #DFE6EE; }

/* COLORS */
/* FONTS */
.a-row {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto; }
  .a-row.is-justify-flex-end {
    justify-content: flex-end; }
  .a-row.is-justify-center {
    justify-content: center; }
  .a-row.is-justify-space-between {
    justify-content: space-between; }
  .a-row.is-justify-space-around {
    justify-content: space-around; }
  .a-row.is-justify-space-evenly {
    justify-content: space-evenly; }
  .a-row.is-align-flex-end {
    align-items: flex-end; }
  .a-row.is-align-center {
    align-items: center; }
  .a-row.is-align-baseline {
    align-items: baseline; }
  .a-row.is-align-stretch {
    align-items: stretch; }
  .a-row.is-content-flex-end {
    align-content: flex-end; }
  .a-row.is-content-center {
    align-content: center; }
  .a-row.is-content-baseline {
    align-content: baseline; }
  .a-row.is-content-stretch {
    align-content: stretch; }
  .a-row.is-direction-row {
    flex-direction: row; }
  .a-row.is-direction-row-reverse {
    flex-direction: row-reverse; }
  .a-row.is-direction-column {
    flex-direction: column; }
  .a-row.is-direction-column-reverse {
    flex-direction: column-reverse; }
  .a-row.is-wrap {
    flex-wrap: wrap; }
  .a-row.is-nowrap {
    flex-wrap: nowrap; }
  .a-row.is-nowrap {
    flex-wrap: nowrap; }
  .a-row.is-wrap-reverse {
    flex-wrap: wrap-reverse; }
